
/**
 * @description This function converts sort option to proper query strings that server demands
 * @param option the sort option
 * @returns queryString Proper query string
 */
export const convertToSortQueryString = (option) => {
    switch (option) {
        case 'Project ID': return 'id';
        case 'Project Name': return 'name';
        case 'Start Date': return 'createdat';
        default: return option;
    }
};

/**
 * @description This function converts active option to proper query strings that server demands
 * @param option the active option
 * @returns queryString Proper query string
 */
export const convertToActiveQueryString = (option) => {
    switch (option) {
        case 'All': return 'all';
        case 'Active': return 'true';
        case 'Inactive': return 'false';
        default: return option;
    }
};
