import { ProjectRequest } from '../core-data/projects/projectRequest';

export const ProjectValidation = (project: ProjectRequest) => {
    let isValid = true;
    const errors = {
        isValid: false,
        about: '',
        projectName: '',
        clientId: '',
        duration: '',
        requiredSkills: '',
        resources: ''
    };

    if (project.about.trim().split(' ').length < 10) {
        errors.about = 'Project description should have minimum 10 words';
        isValid = false;
    }
    if (project.projectName.trim().length < 5) {
        errors.projectName = 'Project Name should have minimum 5 characters';
        isValid = false;
    }
    if (!project.clientID) {
        errors.clientId = 'Client must be assigned';
        isValid = false;
    }
    if (!(project.startDate || project.endDate)) {
        errors.duration = 'Duration must be specified';
        isValid = false;
    }
    if (!project.requiredSkills.length) {
        errors.requiredSkills = 'Assign skills for the project';
        isValid = false;
    }
    if (!project.resources.length) {
        errors.resources = 'Assign resources for the project';
        isValid = false;
    }
    errors.isValid = isValid;
    return errors;
};
