import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { ProjectList } from './projectList';
import { Project } from './project';
import { PaginatedResult } from './paginatedResult';
import { convertToSortQueryString, convertToActiveQueryString } from '../../utils/queryUtils';
import { ProjectRequest } from './projectRequest';
import { ProjectResponse } from './projectResponse';
import { environment } from '../../../../environments/environment';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

const BASE_URL = environment.SPRINGBOOT_API_ENDPOINT;

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {
  // listmodel = 'projectslist';
  model = 'projects';

  constructor(private httpClient: HttpClient) { }

  getProjects = (page: number, items: number, sortby: string, active: string) => {
    sortby = convertToSortQueryString(sortby);
    active = convertToActiveQueryString(active);
    return this.httpClient.get<PaginatedResult>(`${BASE_URL}${this.model}?page=${page}&limit=${items}&sortby=${sortby}&active=${active}`)
      .pipe(catchError((error: HttpErrorResponse) => throwError(error)));
  }

  getProject = (id: number) => {
    return this.httpClient.get<Project>(`${BASE_URL}${this.model}/${id}`);
  }

  deleteProject = (id: number) => {
    return this.httpClient.put(`${BASE_URL}${this.model}/${id}`, {
      isDeleted: true
    });
  }

  editProject = (project: ProjectRequest, id: number) => {
    return this.httpClient.put<ProjectResponse>(`${BASE_URL}${this.model}/update/${id}`, project);
  }

  addProject = (project: ProjectRequest) => {
    return this.httpClient.post<ProjectResponse>(`${BASE_URL}${this.model}`, project);
  }
}
